import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import { Text, Localizer } from 'preact-i18n';

import PubSub, { topics } from '../../lib/pubsub';
import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();

const MAX_LOADINDEX = mu.getMaxLoadIndex();

import Map from '../../components/map';
import ImageSlider from '../../components/imageSlider';
import WorkoutGraph from '../../components/workout/graph';
import TrackInfoGraph from '../../components/track/infoGraph';
import Gauge from '../../components/gauge';

const initialState = {
    newTrack: {},
    showAdminInfo: false,
};

@observer
class TracksDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadTrack(props = this.props) {
		const { trackStore, userStore, workoutPlanStore } = this.props.stores;
        await trackStore.load(props.track);
        const { track } = trackStore;

        workoutPlanStore.updateItem('workoutPlans', []);
		workoutPlanStore.updateItem('workoutPlan', {});
        workoutPlanStore.resetGraphPlanWeekDistanceCurrent();

        await userStore.getWorkoutSummary({ track: track.id });
    }

	deleteTrack = async () => {
		const { trackStore } = this.props.stores;
		const { track } = trackStore;
        await trackStore.saveField(track.id, 'offline', 1);
		PubSub.publish(topics.STATUS_MESSAGE, {
			message: 'Runden er slettet',
			type: 'success',
			icon: 'fas fa-check',
		});
        route(`/tracks/`, true);
	}

    toggleAdminInfo = () => {
		const { userStore } = this.props.stores;
		const { isAdmin } = userStore;
		if (isAdmin) {
			const { showAdminInfo } = this.state;
			this.setState({
				showAdminInfo: !showAdminInfo,
			});
		}
	}

    back = () => {
        route(`/tracks/`, true);
    }

	componentDidMount() {
        this.loadTrack();
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.track !== this.props.track) {
            this.loadTrack(nextProps);
        }
    }

	render() {
		const { trackStore, userStore } = this.props.stores;
        const { submenu, showAdminInfo } = this.state;
        const { isAdmin, graphWorkoutSummaryWeekDistanceCurrentTotal, user = {} } = userStore;
        const { teams = [] } = user;
		const { track } = trackStore;
        const { gpx, gpxInfo = {} } = track;
        const { weightedTotalKcal = 0, loadIndex = 0 } = gpxInfo;
		const loadMultiplier = loadIndex / 100;

        const isAllowedToEdit = track.user === user.id || teams.indexOf(track.team) > -1;

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='d-flex justify-content-between'>
                    <div>
                        <button class='btn btn-link pl-0 fixed-top'
                            style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 25px; width: 40px;`}
                            onClick={this.back}>
                            <i class='fas fa-arrow-circle-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>&nbsp;</h5>
                    </div>
                    <div>
                        {isAllowedToEdit && <a class='btn btn-sm btn-primary float-right'
                            href={`/tracks/edit/${track.id}`}
                        >
                            <i class='fas fa-edit' /> <Text id='tracks.edit'>Endre</Text>
                        </a>}
                    </div>
                </div>

                <div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <div class='d-flex mb-2'>
                            {/* {JSON.stringify(dog, null, 4)} */}
                            <div class={`text-center border rounded-circle imageRounded`} style={track.image ? `background-image: url("${track.image.s3SmallLink}");` : ''}>
                                {!track.image && <i class='fas fa-map-marked text-muted mt-3' style='font-size: 40px;' />}
                            </div>
                            <div class='flex-grow-1 pl-3'>
                                <h5>{track.name}</h5>
                                <i class='fas fa-calendar-alt mr-1' />
                                {track.updatedDate ? util.isoDate(track.updatedDate, false, false, true) : util.isoDate(track.createdDate, false, false, true)}<br />
                                {track.public === 1 && <span class='badge badge-success'>Is public</span>}
                            </div>
                        </div>
                        {graphWorkoutSummaryWeekDistanceCurrentTotal > 0 && <>
                            <WorkoutGraph stores={this.props.stores} isLoading={false} />
                            {/* {util.format(graphWorkoutSummaryWeekDistanceCurrentTotal, 0)} km */}
                        </>}

                        {track.gpx && <div class='mb-2' style='height: 500px;'>
                            <Map stores={this.props.stores} mapId={track.id} geojson={track.gpx.geoJSON} gpxInfo={track.gpx.gpxInfo} />
                        </div>}


                        <div class='container-fluid mb-2'>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-road' /> <Text id='tracks.distance'>Distance</Text></small>
                                    <h3>
                                        {util.format(track.distanceKm, 1)} km
                                    </h3>
                                </div>
                                <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> <Text id='tracks.elevation'>Elevation</Text></small>
                                    <h3>
                                        {util.format(track.elevation, 0)} m
                                    </h3>
                                </div>
                            </div>
                        </div>

                        {(util.checkNested(track, 'gpxInfo', 'elevations') || util.checkNested(track, 'gpx', 'gpxInfo', 'elevations')) && <TrackInfoGraph
                            stores={this.props.stores}
                            key={`track-elevation-${track.id}`}
                        />}

                        {loadIndex > 0 && <div class='row mt-4'>
                            <div class='col text-center'>
                                <h5 onClick={this.toggleAdminInfo}>
                                    <Text id='workouts.load-index'>Belastningsindex</Text>
                                </h5>
                                <Gauge key={`gauge-${loadIndex}`}
                                    radius={100}
                                    showPercentText={true}
                                    color={mu.getLoadIndexColor(util.normalizeBetween(loadIndex, 0, MAX_LOADINDEX, 0, 100))}
                                    percent={Math.floor(util.normalizeBetween(loadIndex, 0, MAX_LOADINDEX, 0, 100))}
                                    completed={mu.getLoadIndexText(util.normalizeBetween(loadIndex, 0, MAX_LOADINDEX, 0, 100))}
                                    prefix=''
                                />
                                <div>
                                    <span class='badge badge-pill badge-secondary'>Long distance</span>
                                    <span class='badge badge-pill badge-secondary ml-2'>Serious amature</span><br />
                                </div>
                                
                                {isAdmin && showAdminInfo && <div>
                                    <small>
                                        <span class='badge badge-secondary mr-2'>loadIndex: {gpxInfo.loadIndex}</span>
                                        <span class='badge badge-secondary mr-2'>Max loadIndex: {MAX_LOADINDEX}</span>
                                        <span class='badge badge-secondary mr-2'>totalKcalRelated: {util.format(gpxInfo.totalKcalRelated, 1)}</span>
                                        <span class='badge badge-secondary mr-2'>normalSpeedMs: {gpxInfo.normalSpeedMs}</span>
                                        <span class='badge badge-secondary mr-2'>normalDuration: {gpxInfo.normalDuration}</span>
                                        <span class='badge badge-secondary mr-2'>durationFactor: {util.format(gpxInfo.durationFactor, 2)}</span>
                                        <div>
                                            <small class='text-muted'>
                                                loadIndex = Math.floor(totalKcalRelated * durationFactor * speedFactor * typeFactor * ambitionFactor);
                                            </small>
                                        </div>
                                    </small>
                                </div>}
                            </div>
                        </div>}


                        <div class='container-fluid mb-2 px-0'>
                            <div class='row'>
                                <div class='col-12'>
                                    <div class='d-flex mx-0'>
                                        {track.images && <ImageSlider images={track.images} />}
                                    </div>    
                                </div>
                            </div>
						</div>


                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-5'>
                            {isAllowedToEdit && <button class='btn btn-sm btn-danger mt-5 float-right'
                                onClick={this.deleteTrack}
                            >
                                <i class='fas fa-trash' /> <Text id='tracks.delete'>Slett</Text> {track.name}
                            </button>}
                        </div>

					</div>
				</div>
			</div>
		);
	}
}

export default TracksDetail;
